import { NextResponse } from "next/server"

export async function GET(request: Request) {
  const { searchParams } = new URL(request.url)
  const channelId = searchParams.get("channelId")
  const apiKey = process.env.YOUTUBE_API_KEY // User needs to set this in .env

  if (!channelId) {
    return NextResponse.json({ error: "Channel ID is required" }, { status: 400 })
  }

  if (!apiKey) {
    return NextResponse.json({ error: "YouTube API Key is not configured" }, { status: 500 })
  }

  try {
    const youtubeApiUrl = `https://www.googleapis.com/youtube/v3/search?key=${apiKey}&channelId=${channelId}&part=snippet,id&order=date&maxResults=5`
    const response = await fetch(youtubeApiUrl)
    const data = await response.json()

    if (!response.ok) {
      console.error("YouTube API error:", data)
      return NextResponse.json({ error: data.error?.message || "Failed to fetch YouTube videos" }, { status: response.status })
    }

    const videos = data.items.map((item: any) => ({
      id: item.id.videoId,
      title: item.snippet.title,
      thumbnail: item.snippet.thumbnails.high.url,
    }))

    return NextResponse.json(videos)
  } catch (error) {
    console.error("Server error fetching YouTube videos:", error)
    return NextResponse.json({ error: "Internal server error" }, { status: 500 })
  }
}
